        if (oldValue !== newValue) {
          this.uploadImageUrl = '';
          this.generateData[oldValue].isActivated = false;
          this.generateData[newValue].isActivated = true;

          if (oldValue === 'inpaint') {
            this.handleClearInpaint();
          }
        }
      }
    }
  },
