    },
    isUseSketch() {
      return this.modeSetting.generateType === 'txt2img';
    },
    isUseInpaint() {
      return this.modeSetting.generateType === 'inpaint';
    },
    isUseRendering() {
      return this.modeSetting.generateType === 'img2img';
    },
  },
  methods: {
    handleGenerate() {
      if (this.modeSetting.prompt === '') {
        this.$message.error('Failed to generate: No prompt submitted.');
        return;
      }

      let config = this.handlePrepareGeneralConfig();
      console.log('config:', config)
      if (this.isUseSketch) this.handleTxt2img(config);
      else if (this.isUseInpaint) this.handleInpaint(config);
      else if (this.isUseRendering) this.handleImg2img(config);

      // percent
      this.handleGetProgress();
    },
    handlePrepareGeneralConfig() {
      let type = this.modeSetting.generateType;
      let generateType = this.generateData[type];

      let resolutionLabel = this.modeSetting.resolution, resolutionOptions = this.modeChoiceResolutionOptions;
      let resolution = resolutionOptions[resolutionOptions.findIndex(item => item.label === resolutionLabel)];

      let negative_prompt = initConfig.default.negativePrompt;
      let choicePromptStyle = this.choicePromptStyle;
      if (choicePromptStyle.name !== '' && choicePromptStyle.negative_prompt !== '') {
        negative_prompt = choicePromptStyle.negative_prompt;
      }

      this.generateData[type].generateTaskId = uuidv4();

      let config = {
        sd_model_checkpoint: this.modeSetting.mode,
        id_task: this.generateData[type].generateTaskId,
        width: resolution.value.width,
        height: resolution.value.height,
        prompt: this.modeSetting.prompt,
        negative_prompt: negative_prompt,
        batch_size: this.modeSetting.batchSize,
        sampler_index: this.modeSetting.samplerMethod,
        n_iter: 1,
        seed: -1,
        steps: 20,
        alwayson_scripts: {},
      };

      if (!this.isUseInpaint) {
        config.alwayson_scripts = {
          controlnet: {
            args: [
              {
                "enabled": true,
                "input_image": generateType.uploadImageUrl,
                "control_mode": this.identifyControlMode(generateType.controlModeIndex),
                "module": this.isUseSketch ? initConfig.default.sketchModule : initConfig.default.renderingModule,
                "model": this.isUseSketch ? initConfig.default.sketchModel : initConfig.default.renderingModel,
                "mask": null,
                "lowvram": "false",
                "pixel_perfect": "false",
                "weight": 1,
                "resize_mode": 1,
                "processor_res": 512,
                "threshold_a": 100,
                "threshold_b": 200,
                "guidance": 1,
                "guidance_start": 0,
                "guidance_end": 1,
              }
            ]
          }
        };
      }
      return config;
    },
    handleTxt2img(config) {
      this.$message.success('Please wait a moment to start generating pictures.');
      this.generateData.txt2img.isGenerating = true;

      api.txt2img(config)
        .then(res => {
          let imgList = res.images;
          this.generateData.txt2img.generateImageList = [];

          if (this.generateData.txt2img.isShowCarousel) {
            this.handleCloseCarousel('txt2img');
          }

          this.generateData.txt2img.generateProgress = 100;
          imgList.forEach(item => {
            this.generateData.txt2img.generateImageList.push({
              imgUrl: 'data:image/png;base64,' + item
            });
          });
        })
        .catch(err => {
          console.error(err)
        })
        .finally(() => {
          this.generateData.txt2img.isGenerating = false;
        })
    },
    handleInpaint(config) {
      let canvas = document.getElementById('canvas');
      let image = document.getElementById('image');
      config.denoising_strength = this.generateData.inpaint.repaintStrength;

      this.convertCanvasBackground(canvas, image.src)
        .then((maskUrl) => {
          config.mask = maskUrl;
          config.mask_blur = 4; // 
          config.inpainting_fill = 1; //  0 1 2 3
          config.inpaint_full_res_padding = 32; // Only masked padding, pixels 32
          config.inpainting_mask_invert = 0; //  0 1 
          config.inpaint_full_res = false; // inpaint area, False: whole picture Trueonly masked
          this.handleImg2img(config);
        });
    },
    handleImg2img(config) {
      let type = this.modeSetting.generateType;
      let url = this.generateData[type].uploadImageUrl;
      if (url === '') {
        this.$message.error('Please prepare a picture first');
        return;
      }

      this.$message.success("Please wait a moment to start generating pictures.");
      config.init_images = [url];
      config.resize_mode = 0; // ,,,
      this.generateData[type].isGenerating = true;

      api.img2img(config)
        .then(res => {
          let imgList = res.images;
          this.generateData[type].generateImageList = [];

          if (this.generateData[type].isShowCarousel) {
            this.handleCloseCarousel(type);
          }

          this.generateData[type].generateProgress = 100;
          imgList.forEach(item => {
            this.generateData[type].generateImageList.push({
              imgUrl: 'data:image/png;base64,' + item
            });
          });
        })
        .catch(err => {
          console.error(err)
        })
        .finally(() => {
          this.generateData[type].isGenerating = false;
        })
    },
    handleGetProgress() {
      let type = this.modeSetting.generateType;
      this.generateData[type].generateProgress = 0;
      this.generateData[type].progressTimer = setInterval(() => {
        if (this.generateData[type].isGenerating) {
          this.getProgress(type);
        } else {
          clearInterval(this.generateData[type].progressTimer);
        }
      }, 1000);
    },
    identifyControlMode(index) {
      return index === -1 ? 1 : index === 1 ? 2 : 0;
    },
    // api
    getSDModels() {
      api.getModels()
        .then(res => {
          // this.modeChoiceCheckPointOptions.push(...res);
          this.modeChoiceCheckPointOptions = res;
        })
        .catch(err => {
          console.error(err)
        })
    },
    getLoRAs() {
      api.getLoRAs()
        .then(res => {
          // this.modeChoiceCustomizedOptions.push(...res);
          this.modeChoiceCustomizedOptions = res;
        })
        .catch(err => {
          console.error(err)
        })
    },
    getPromptPresuppose() {
      api.getStyleList()
        .then(res => {
          if (res.success && res.data.StyleList && res.data.StyleList.length > 0) {

            this.modeChoicePromptOptions = res.data.StyleList.map(item => ({
              ...item,
              isDisabled: true
            }));
          } else {
            // using the default value
            console.log('%c [ StyleList is [] ]-603', 'font-size:13px; background:pink; color:#bf2c9f;', res.data.StyleList)
          }
        })
        .catch(err => {
          console.error(err)
        });
    },
    getProgress(type) {
      api.getGenerateProgress({
        id_task: this.generateData[type].generateTaskId,
        id_live_preview: -1
      })
        .then(res => {
          const { active, progress, completed, queued } = res;
          if (active) {
            this.generateData[type].generateProgress = Number((progress * 100).toFixed(2));
          } else if (queued) {
            this.generateData[type].generateProgress = 0;
          } else if (completed) {
            this.generateData[type].generateProgress = 99;
          } else {
            this.generateData[type].generateProgress = 0;
          }
        })
        .catch(err => {
          console.log('[ err ] >', err)
        })
    },
    addStyle(style) {
      api.setStyle(style)
        .then(res => {
          if (res.success) {
            this.modeChoicePromptOptions.push({
              ...res.data,
              isDisabled: true,
            });
            this.$message.success('Successfully Saved!');
          } else {
            this.$message({
              type: 'error',
              message: 'Save failure!'
            });
          }
        })
        .catch(err => {
          console.log(err)
        });
    },
    // 
    handleChangeGenerateType(item) {
      this.modeSetting.generateType = item;
    },
    handleChangeCustomizedStyle(item) {
      const regex = new RegExp("<lora:" + item + ":(\\d+\.\\d+|\\d+)>", "g");
      let prompt = this.modeSetting.prompt;
      const match = regex.exec(prompt);

      if (match) {
        this.modeSetting.prompt = prompt.replace(regex, '')
      } else {
        this.modeSetting.prompt = `${prompt}<lora:${item}:1>`;
      }
    },
    handleChangePresupposePrompt(name) {
      let choice = this.modeChoicePromptOptions.find(item => item.style_name === name);
      this.choicePromptStyle = Object.assign({}, choice);
      this.modeSetting.prompt = choice.prompt_text;
    },
    isRepeatPromptStyleName(value) {
      return this.modeChoicePromptOptions.some(item => {
        return item.style_name === value ? true : false;
      })
    },
    handleSavePresuppose() {
      let tmp = this.modeSetting.prompt;
      if (tmp.length === 0) {
        this.$message.error('Please enter the prompt')
      } else {
        this.$prompt('Please enter a prompt style name', 'tip', {
          confirmButtonText: 'confirm',
          cancelButtonText: 'cancel',
          inputPattern: /^.+$/,
          inputErrorMessage: 'The prompt style name is empty',
          customClass: 'save-prompt-style-box'
        }).then(({ value }) => {

          let hasRepeat = this.isRepeatPromptStyleName(value);
          if (hasRepeat) {
            this.$message.error('The style name is repeated')
            return;
          }
          // Save the prompt
          let style = {
            style_name: value,
            prompt_text: tmp,
            negative_prompt_text: '',
          }
          this.addStyle(style);
        }).catch(() => {
          this.$message({
            type: 'warning',
            message: 'Cancel Save!'
          });
        });
      }
    },
    // 
    beforeUpload(file) {
      const isLt5M = file.size / 1024 / 1024 < 5;

      if (!isLt5M) {
        this.$message.error('The size of uploaded profile picture cannot exceed 5MB.');
      }
      return isLt5M;
    },
    handleUpload(file) {
      this.blobToBase64(file.file)
        .then(res => {
          let type = this.modeSetting.generateType;
          this.generateData[type].uploadImageUrl = res;
          this.generateData[type].isShowUpload = false;
        })
    },
    blobToBase64(blob) {
      return new Promise((resolve, reject) => {
        const fileReader = new FileReader();
        fileReader.onload = (e) => {
          resolve(e.target.result);
        };
        fileReader.readAsDataURL(blob);
        fileReader.onerror = () => {
          reject(new Error('blobToBase64 error'));
        };
      });
    },
    // 
    handleImgOnload() {
      if (this.isUseInpaint) {
        this.initCanvas();
      }
    },
    handleRefreshImage() {
      if (this.drawHistory.length > 0) {
        this.ctx.putImageData(this.drawHistory.pop(), 0, 0);
      }
    },
    handleCloseImage(type) {
      this.generateData[type].uploadImageUrl = '';
      this.generateData[type].isShowUpload = true;
    },
    handleClearInpaint() {
      let canvas = document.getElementById('canvas');
      this.ctx.clearRect(0, 0, canvas.width, canvas.height);
      this.drawHistory = [];
      this.isShowBrushSize = false;
    },
    handleToggleBrushSize() {
      this.isShowBrushSize = !this.isShowBrushSize;
    },
    // 
    initCanvas() {
      let image = document.getElementById('image');
      let canvas = document.getElementById('canvas');
      canvas.width = image.width;
      canvas.height = image.height;
      this.ctx = canvas.getContext('2d');
      this.ctx.fillStyle = "red";
      this.ctx.fill();

      // 
      canvas.addEventListener('mousedown', (e) => {
        this.isDrawing = true;
        [this.lastX, this.lastY] = [e.offsetX, e.offsetY];

        let canvas = document.getElementById('canvas');
        let image = this.ctx.getImageData(0, 0, canvas.width, canvas.height);
        this.drawHistory.push(image);
      });
      // 
      canvas.addEventListener('mousemove', (e) => {
        if (!this.isDrawing) return;
        this.drawMask(e.offsetX, e.offsetY);
      });
      // 
      canvas.addEventListener('mouseup', (e) => {
        this.isDrawing = false;
      });
    },
    drawMask(endX, endY) {
      this.ctx.beginPath();
      this.ctx.moveTo(this.lastX, this.lastY);
      this.ctx.lineTo(endX, endY);
      this.ctx.strokeStyle = 'rgba(0, 0, 0, 1)';
      this.ctx.lineWidth = this.brushSize;
      this.ctx.lineCap = 'round';
      this.ctx.stroke();
      [this.lastX, this.lastY] = [endX, endY];
    },
    convertCanvasBackground(_canvas, _image) {
      return new Promise(async (resolve) => {
        const canvas = _canvas;
        const ctx = canvas.getContext('2d');
        const imageData = ctx.getImageData(0, 0, canvas.clientWidth, canvas.clientHeight);
        const imageDataContent = imageData.data;

        // 
        for (let i = 0; i < imageDataContent.length; i += 4) {
          // 
          if (imageDataContent[i + 3] < 128) {
            // 
            imageDataContent[i] = 0;
            imageDataContent[i + 1] = 0;
            imageDataContent[i + 2] = 0;
            imageDataContent[i + 3] = 255;
          } else {
            // 
            imageDataContent[i] = 255;
            imageDataContent[i + 1] = 255;
            imageDataContent[i + 2] = 255;
            imageDataContent[i + 3] = 255;
          }
        }

        // 
        const exportCanvas = document.createElement('canvas');
        exportCanvas.width = canvas.clientWidth;
        exportCanvas.height = canvas.clientHeight;
        const exportCtx = exportCanvas.getContext('2d');
        exportCtx.putImageData(imageData, 0, 0);
        // mask
        const size = await this.getImageOriginSize(_image);
        const finalMask = await this.scaleImage(exportCanvas.toDataURL(), size.width, size.height);
        resolve(finalMask);
      });
    },
    async getImageOriginSize(src) {
      return new Promise((resolve) => {
        const image = new Image();
        image.src = src;
        image.onload = () => {
          resolve({
            width: image.naturalWidth,
            height: image.naturalHeight
          });
        };
      });
    },
    async scaleImage(src, width, height) {
      return new Promise((resolve) => {
        const image = new Image();
        image.src = src;
        image.onload = () => {
          const canvas = document.createElement('canvas');
          canvas.width = width;
          canvas.height = height;
          const ctx = canvas.getContext('2d');
          ctx.drawImage(image, 0, 0, width, height);
          resolve(canvas.toDataURL());
        };
      })
    },
    // 
    handleClickGenerateImage(index, type) {
      this.generateData[type].isShowCarousel = true;
      this.generateData[type].generateCarouselIndex = index;
    },
    handleCloseCarousel(type) {
      this.generateData[type].isShowCarousel = false;
      this.generateData[type].generateCarouselIndex = 0;
    },
    handleSentSave() {
      let type = this.modeSetting.generateType;
      if (this.generateData[type].generateImageList.length === 0) {
        this.$message.error("Please generate an image first");
        return;
      }
      const { generateImageList, generateCarouselIndex } = this.generateData[type];

      let aLink = document.createElement('a');
      aLink.style.display = "none";
      aLink.href = generateImageList[generateCarouselIndex].imgUrl;
      aLink.download = "downloadImage.png";
      document.body.appendChild(aLink);
      aLink.click();
      document.body.removeChild(aLink);
    },
    dataURL2Blob(dataUrl) {
      var arr = dataUrl.split(","),
        mime = arr[0].match(/:(.*?);/)[1],
        bstr = atob(arr[1]),
        n = bstr.length,
        u8arr = new Uint8Array(n);
      while (n--) {
        u8arr[n] = bstr.charCodeAt(n);
      }
      return new Blob([u8arr], {
        type: mime,
      });
    },
    handleSentFaceliftOrRendering(to) {
      let from = this.modeSetting.generateType;

      if (from === to) return;
      if (this.generateData[from].generateImageList.length === 0) {
        this.$message.error("Please generate an image first");
        return;
      }

      const { generateImageList, generateCarouselIndex } = this.generateData[from];

      this.modeSetting.generateType = to;
      this.$nextTick(() => {
        this.generateData[to].uploadImageUrl = generateImageList[generateCarouselIndex].imgUrl;
        this.generateData[to].isShowUpload = false;
      })
    },
    // Dialog
    handleCloseDialog(done) {
      done();

      this.activeDialogTab = '2';
      this.isCreateCustomized = false;
      this.isCreateModeCheckPoint = false;
      this.isCreatePrompt = false;
    },
    // Dialog-Tab-Customized
    handleToggleEnableCustomized() {
      this.isEnableCustomized = !this.isEnableCustomized;

      if (!this.isEnableCustomized) {
        // this.modeSetting.customizedStyle = '';
        this.dialogModeConfig.modeCustomized = '';
      }
    },
    handleSelectCustomized(item) {
      this.dialogModeConfig.modeCustomized = item;
    },
    handleCreateCustomized() {
      let item = this.dialogModeConfig.modeCustomized;
      this.modeChoiceCustomizedOptions.push({ name: item });
      this.dialogModeConfig.modeCustomized = '';
    },
    handleDeleteCustomized() {
      if (!this.isEnableCustomized) {
        return;
      }

      let index = this.modeChoiceCustomizedOptions.findIndex(item => item.name === this.dialogModeConfig.modeCustomized);
      if (index !== -1) {
        this.modeChoiceCustomizedOptions.splice(index, 1);
        this.dialogModeConfig.modeCustomized = '';
      } else {
        this.$message.error('Please refresh and try again');
      }
    },
    handleUploadCustomizedStyle(file) {
      this.isUploadLoading = true;
      let params = new FormData()
      params.append('file', file.file)
      api.uploadLoRAs(params)
        .then(res => {
          this.$message.success(`Upload successful, file name is${res.networks[0].name}`)
          this.getLoRAs();
        })
        .catch(err => {
          this.$message.error(`Upload failed`);
          console.error(err)
        })
        .finally(() => {
          this.isUploadLoading = false;
        })
    },
    // Dialog-Tab-Mode
    handleSelectCheckPoint(item) {
      this.dialogModeConfig.modeCheckPoint = item;
    },
    handleCreateModeCheckPoint() {
      let item = this.dialogModeConfig.modeCheckPoint;
      this.modeChoiceCheckPointOptions.push({ title: item });
      this.dialogModeConfig.modeCheckPoint = '';
    },
    handleDeleteCheckPoint() {
      let index = this.modeChoiceCheckPointOptions.findIndex(item => item.title === this.dialogModeConfig.modeCheckPoint);
      if (index !== -1) {
        this.modeChoiceCheckPointOptions.splice(index, 1);
        this.dialogModeConfig.modeCheckPoint = '';
      } else {
        this.$message.error('Please refresh and try again');
      }
    },
    // Dialog-Tab-Prompt
    handleDeletePrompt(index) {
      if (index >= 0 && index < this.modeChoicePromptOptions.length) {
        api.delStyle({ "style_id": this.modeChoicePromptOptions[index].id })
          .then(res => {
            if (res.success) {
              this.modeChoicePromptOptions.splice(index, 1);
              this.$message.success('Successfully deleted');
            }
          })
          .catch(err => {
            console.log(err)
          });
      } else {
        this.$message.error('Please refresh and try again');
      }
    },
    handleEditPrompt(index) {
      let flag = this.modeChoicePromptOptions[index].isDisabled;
      this.modeChoicePromptOptions[index].isDisabled = !flag;
      if (flag) {
        this.$message.success('Please modify your prompt')
      } else {
        this.$message.warning('Stop modifying prompt')
      }
    },
    handleSubmitEditPrompt(item, index) {
      const { isDisabled, ...prompt } = item;
      api.editStyle(prompt)
        .then(() => {
          this.$message.success('Successfully edited');
        })
        .catch(err => {
          console.log(err)
        });
      this.modeChoicePromptOptions[index].isDisabled = true;
    },
    handleCancelCreatePrompt() {
      this.dialogModeConfig.modePrompt = {
        style_name: '',
        prompt_text: '',
        negative_prompt_text: '',
      };
      this.isCreatePrompt = false;
    },
    handleCreatePrompt() {
      if (!this.isCreatePrompt) {
        this.isCreatePrompt = true;
        this.$nextTick(() => {
          let container = document.getElementsByClassName('setting-prompt-container')[0];
          if (container) {
            container.scrollTo({
              top: container.scrollHeight,
              behavior: 'smooth'
            });
          }
        });
      }
    },
    handleSavePrompt() {
      let promptStyle = this.dialogModeConfig.modePrompt;
      if (promptStyle.style_name !== '') {
        let hasRepeat = this.isRepeatPromptStyleName(promptStyle.style_name);
        if (hasRepeat) {
          this.$message.error('The style name is repeated')
          return;
        }
        this.addStyle(promptStyle);
        this.handleCancelCreatePrompt();
      } else {
        this.$message.error('The default name is not filled in, please supplement!');
      }
    },
  },
}
</script>
